<template>
  <v-btn :block="block" :large="large" class="btn" :class="{ 'disable-events': disabled, active }" @click="$emit('click')" :style="{ background: !active ? `${color} !important` : '' }">
    <slot name="leftLogo"></slot>
    {{ title }}
    <slot name="rightLogo"></slot>
  </v-btn>
</template>

<script>
export default {
  props: {
    block: {
      type: Boolean,
      default: false,
    },
    large: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    active: {
      type: Boolean,
      default: false,
    },
    title: {
      type: String,
      default: "No Title",
    },
    color: {
      type: String,
      default: "#0d1015",
    },
  },
};
</script>

<style scoped>
.btn {
  background: #0d1015 !important;
  border: 1px solid rgba(106, 164, 247, 0.6) !important;
  box-sizing: border-box !important;
  box-shadow: 0px 0px 9px 4px #1c1d1e !important;
  border-radius: 10px !important;
  color: white;
}
.disable-events {
  pointer-events: none;
  opacity: 0.5;
}
.active {
  background: white !important;
  color: midnightblue !important;
}
</style>
