<template>
  <MainLayout :pageTitle="`Configurations`">
    <div class="configurations-main">
      <div style="width: 100%">
        <v-row no-gutters style="border-bottom: 1px solid white" class="py-4 px-2">
          <v-col cols="11">
            <v-row no-gutters class="configurations-text configurations-title"> View Performance History </v-row>
            <v-row no-gutters class="configurations-text"> Allow/Disallow reviewers to be able to see their performance history </v-row>
          </v-col>
          <v-col cols="1" class="configurations-input-alignment">
            <v-switch v-model="reviewSwitch" color="green" hide-details :ripple="false"></v-switch>
          </v-col>
        </v-row>
        <v-row no-gutters style="border-bottom: 1px solid white" class="py-4 px-2">
          <v-col cols="11">
            <v-row no-gutters class="configurations-text configurations-title"> Faulty Review Score Threshold </v-row>
            <v-row no-gutters class="configurations-text"> Review accuracies that are lower than the threshold will be accepted as a faulty review. </v-row>
          </v-col>
          <v-col cols="1" class="configurations-input-alignment">
            <v-text-field :rules="thresholdInputRules" v-model="threshold">
              <v-icon small slot="append" color="white"> mdi-percent </v-icon>
            </v-text-field>
          </v-col>
        </v-row>
      </div>
      <div style="width: 100%; gap: 15px" class="d-flex justify-end">
        <Button title="Discard" @click="discard" :disabled="isUnchanged"></Button>
        <Button title="Save and Apply" @click="save" :disabled="isSaveButtonDisabled"></Button>
      </div>
    </div>
  </MainLayout>
</template>

<script>
import MainLayout from "@/components/MainLayout";
import Button from "@/components/Button";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "Configurations",
  components: { MainLayout, Button },
  data: () => ({
    originalData: {
      reviewSwitch: false,
      threshold: 0,
    },
    reviewSwitch: true,
    threshold: 75,
  }),
  methods: {
    ...mapActions(["updateRepoConfig"]),
    setOriginalData() {
      const { faultyThreshold, seeSelfPerformance } = this.repoConfig;
      this.originalData = { threshold: faultyThreshold, reviewSwitch: seeSelfPerformance };
      this.reviewSwitch = seeSelfPerformance;
      this.threshold = faultyThreshold;
    },
    async updateRepositoryConfigurations() {
      await this.updateRepoConfig({
        repoUrl: this.$route.params.repoUrl,
        config: { faultyThreshold: Number(this.threshold), seeSelfPerformance: this.reviewSwitch },
      });
    },
    discard() {
      this.threshold = this.originalData.threshold;
      this.reviewSwitch = this.originalData.reviewSwitch;
    },
    async save() {
      if (this.isUnchanged) return;
      await this.updateRepositoryConfigurations();
    },
  },
  computed: {
    ...mapGetters(["repoConfig"]),
    thresholdInputRules: () => [(value) => (value && value >= 0 && value <= 100 && Number.isInteger(Number(value))) || "Invalid Threshold Value"],
    isUnchanged() {
      const thresholdValue = Number(this.threshold); // input of floating points in the text field causes string types
      return this.reviewSwitch === this.originalData.reviewSwitch && thresholdValue === this.originalData.threshold;
    },
    isSaveButtonDisabled() {
      if (this.isUnchanged) {
        return true;
      }
      return !this.threshold || this.threshold < 0 || this.threshold > 100 || !Number.isInteger(Number(this.threshold));
    },
  },
  watch: {
    repoConfig() {
      this.setOriginalData();
    },
  },
};
</script>

<style lang="scss" scoped>
.configurations {
  &-main {
    padding: 25px 35px;
    min-height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
  }
  &-text {
    font-style: normal;
    font-weight: 500;
    font-size: 17px !important;
    line-height: 30px;
    display: flex;
    align-items: center;
    color: #ffffff;
  }
  &-title {
    font-size: 20px !important;
  }
  &-input-alignment {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
</style>
